import React, { Component } from "react";
import axios from 'axios';

import SearchLine from './search-line'
import Characters from './characters'
import ListNavigator from './list-navigator'
import LoadingScreen from './loading-screen'

export default class PeopleSearchEngine extends Component {
  constructor(props) {
    super(props);

    this.state = {
      pageIsLoading: false,
      pageCounter: 0,
      searchLine: "",
      charactersList: [[], []]
    }

    this.searchLineHandler = this.searchLineHandler.bind(this);
    this.pageCounterIsChange = this.pageCounterIsChange.bind(this);
    this.leftArrow = this.leftArrow.bind(this);   
    this.leftArrow = this.leftArrow.bind(this);
    this.rightArrow = this.rightArrow.bind(this);
  }
  
  searchLineHandler(event) {
    this.setState({
      searchLine: event.target.value
    });
  }
  
  pageCounterIsChange(e){
    this.setState({ pageCounter: parseInt(e.target.value) });
  }

  leftArrow() {
    this.setState({ pageCounter: this.state.pageCounter-1 });
  }

  rightArrow() {
    this.setState({ pageCounter: this.state.pageCounter+1 });
  }

  componentDidMount() {
    this.setState({
      pageIsLoading: true,
    });
    const requests = [];
    for(let i = 1; i <= 9; i++) {
      let requestHttp = `https://swapi.co/api/people/?page=${i}`;
      let request = axios.get(requestHttp);
      requests.push(request);
    }  
    axios.all(requests).then(response => {
      this.setState({
        charactersList: response.map(list => { return list.data.results; }),
        pageIsLoading: false
      });
    });
  }
  render() {
    const loadingScreen = this.state.pageIsLoading && <LoadingScreen />;
    const charactersList = this.state.searchLine==="" ? this.state.charactersList : 
      [[].concat(...this.state.charactersList).filter(chari => chari.name.toString().toLowerCase().search(this.state.searchLine.toString().toLowerCase())!== -1 )];
    return (
      <div className="people-search-engine">
        {loadingScreen}
        <SearchLine searchLine={this.state.searchLine} searchLineHandler={this.searchLineHandler}/>
        <Characters charactersList={charactersList[this.state.pageCounter]}/>
        <ListNavigator 
          leftArrow={this.leftArrow}
          rightArrow={this.rightArrow}
          pageCounter={this.state.pageCounter}
          listLength={charactersList.length}
          pageCounterIsChange={this.pageCounterIsChange}
        />
      </div>
    );
  }
}

