import React, { Component } from "react";

export default class NavigatorByNumbers extends Component {
    render() {
        const buttons = [];
        for(let i = 0; i < this.props.listLength; i++) buttons.push(
                <button 
                    key={i} 
                    className={this.props.pageCounter!==i ? "square-navigator" : "square-navigator-center"} 
                    value={i}
                    onClick={(e) => {this.props.pageCounterIsChange(e)}}
                    >{i+1}</button>
            );

        return (
            <React.Fragment>
                {buttons}
            </React.Fragment>
        );
    }
}

