import React, { Component } from "react";

import arrow from "../arrow.png"

import NavigatorByNumbers from './navigator-by-numbers'

export default class ListNavigator extends Component {
    render() {
        const {leftArrow, rightArrow, pageCounter, listLength} = this.props;

        const left = pageCounter!==0 && <button onClick={leftArrow}> 
                    <img src={arrow} height="20" alt="ops"  className="left-arrow"/>
                </button>

        const right = pageCounter!==listLength-1 && <button onClick={rightArrow}>
                    <img src={arrow} height="20" alt="ops" className="right-arrow"/>
                </button>

        return (
            <div className="navigator">
                {left}
                <NavigatorByNumbers listLength={listLength} pageCounter={pageCounter} pageCounterIsChange={this.props.pageCounterIsChange}/>
                {right}
            </div>
        );
    }
}