import React, { Component } from "react";

import PersonsInfo from './persons-info'

import arrow from "../arrow.png"

export default class CharactersItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      personsInfoIsOpen: false,
    }
    this.arrowClick = this.arrowClick.bind(this);
  }
  arrowClick() {
    this.setState({
      personsInfoIsOpen: !this.state.personsInfoIsOpen  
    })
  }
  render() {
    return (
      <React.Fragment> 
        <div className="item-container">
          <div className="name">{this.props.item.name}</div>
          <div className={this.state.personsInfoIsOpen ? "arrow arrow-rotate" : "arrow"}>
            <img src={arrow} height="20" alt="ops" onClick={this.arrowClick}/>
          </div>
        </div>
        <PersonsInfo isOpen={this.state.personsInfoIsOpen} item={this.props.item}/>
      </React.Fragment>
    );
  }
}

