import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";

import AppLoadingButton from './app-loading-button'
import PeopleSearchEngine from './people-search-engine'

export default class Main extends Component {
  render() {
    return (
        <div className="main">
            <Switch>
                <Route path="/" exact component={AppLoadingButton} />
                <Route path="/people-search" exact component={PeopleSearchEngine} />
            </Switch>
        </div>
    );
  }
}

